import React from 'react';

const IconConsole = props => (
  <svg
    x="0px"
    y="0px"
    width={props.width}
    height={props.height}
    viewBox="0 0 128 128"
  >
    <g
      stroke="#fff"
      strokeWidth="8"
      strokeLinecap="square"
      strokeMiterlimit="10"
    >
      <path d="M34.033 89.467L8.566 64M8.566 64l25.467-25.467" />
      <g>
        <path d="M93.967 38.533L119.434 64M119.434 64L93.967 89.467" />
      </g>
      <path d="M42 105.62l45.56-83.24" />
    </g>
  </svg>
);

export default IconConsole;



